import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Employee } from '@app/shared/models/employee.model';
import { ApiResponse } from '@app/shared/models/api-response.model';

@Injectable({
    providedIn: 'root'
})
export class EmployeeService {
    constructor(private http: HttpClient) {

    }
    getAllEmployees() {
        return this.http.get<ApiResponse<Employee>>('api/employee/all?status=1');
    }
    getEmployee(employeeDetailsId: string) {
        return this.http.get<ApiResponse<Employee>>(`api/employee/${employeeDetailsId}`);
    }
    addEmployee(employee: any) {
        return this.http.post<ApiResponse<Employee>>(`api/employee/add`, employee);
    }
    updateEmployee(employee: any) {
        return this.http.put<ApiResponse<Employee>>('api/employee/update', employee)
    }
    activateEmployee(employeeDetailsId: number) {
        return this.http.put<ApiResponse<Employee>>(`api/employee/activate/${employeeDetailsId}`, {})
    }
    deactivateEmployee(employeeDetailsId: number) {
        return this.http.put<ApiResponse<Employee>>(`api/employee/deactivate/${employeeDetailsId}`, {})
    }
    isEmployeeExists(email: string) {
        return this.http.get<ApiResponse<Employee>>(`api/employee/email/exists?email=${email}`)
    }
}